import React, { Component } from 'react';
import Project from './Project.js';
import domtoimage from 'dom-to-image'; // https://github.com/tsayen/dom-to-image
import ProgressiveImage from 'react-progressive-bg-image'; // https://github.com/evenchange4/react-progressive-bg-image

class Content extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imgSrc: 'jason.jpg'
        };
        // this.handleMouseOver = this.handleMouseOver.bind(this);
        // this.handleMouseOut = this.handleMouseOut.bind(this);
    }
    render() {

        return (
            <div className="container" id='content'>
                <img id='origimg' width='500'
                    src={this.state.imgSrc} className="App-logo" alt="logo"
                    style={{
                        display: 'none',
                        border: '1px solid gray'
                    }}
                />
                {/* <img src={logo} className="App-logo" alt="logo" /> */}

                <h1 className="App-title" > Project Portfolio </h1>



                {/* <div className="" style={{
                        backgroundImage: "url(jason.jpg)", height: 600,
                        backgroundSize: 'cover'
                    }}>&ensp;</div> */}
                <p className="flow-text center-align">
                    Computer Scientist. Musician. English Literature and Classical Guitar Student at the University of Costa Rica.
                    </p>

                <div className="parallax-container center-align">
                    {/* <div className="" style={{
                            backgroundImage: "url(jason.jpg)", height: 600,
                            backgroundSize: 'cover'
                        }}>&ensp;</div> */}
                    <div className="parallax">
                        <ProgressiveImage
                            src={'orq.jpg'}
                            placeholder={'orch_small.jpg'}
                            style={{
                                backgroundSize: 'cover',
                                backgroundPosition: 'center bottom',
                            }}
                        />
                        {/* <img src="orq.jpg" alt='Me with the UCR orchestra' /> */}
                    </div>
                </div>

                <div id='projects' />

                <Project
                    title='Dipalicori'
                    description='Dipalicori was one of my college volunteering projects'
                    url={'http://dipalicori.ucr.ac.cr'}
                    images={['1', '2', '3', '4']}
                />

                <Project
                    title='Metics Inventory System'
                    description='This is an inventory system developed for an academic unit at the University of Costa Rica. I worked mostly on the front end, using React JS with Redux, HTML5, CSS3 and Bootstrap 3.'
                    url={''}
                    images={['mts1', 'mts2', 'mts3', 'mts4', 'mts5']}
                />

                {/* <div id='bgImg' className="" style={{
                    backgroundImage: this.state.imgSrc,
                    height: 600,
                    width: 90 + "%",
                    backgroundSize: 'cover',
                }}>&ensp;</div> */}
            </div>

        )
    }
    componentDidMount() {
        // to ensure that the code is run **after** the painting of the newly rendered object

        var node = document.getElementById('root');
        var _this = this;
        window.requestAnimationFrame(function () {
            domtoimage.toJpeg(node, {
                quality: 0.25,
                bgcolor: "white"
                /*style: {filter: 'blur(15px)'}*/
                // better to apply the blur AFTER the image is compressed
            }).then(function (dataUrl) {
                // var bgimgS = document.getElementById('bgImg');
                // bgimgS.style.backgroundImage = "url(" + dataUrl + ")";
                // domImg = dataUrl
                _this.setState({ imgSrc: dataUrl })
                window.blurNAV()
                // var img = document.getElementById('origimg');
                // img.id = 'origimg'
            }).catch(function (error) {
                console.error('oops, something went wrong!', error);
            });
        })


    }
}
export default Content;


