import React, { Component } from 'react';
import Lightbox from 'react-image-lightbox';

class Project extends Component {
    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
            images: this.props.images
        };
    }

    render() {
        const { photoIndex, isOpen, images } = this.state;
        var title = this.props.title;
        var ImgsPath = '/Projects/' + title + '/';

        return (
            <div className=''>
                <h2>{this.props.title}</h2>
                <a className="flexAlign" href={this.props.url} target="_blank">
                    <i className="material-icons">insert_link</i>
                    {this.props.url}
                </a>

                <p className="flow-text">
                    {this.props.description}
                </p>
                {
                    images.map(
                        function (pic, i) {
                            // console.log(i)
                            return (
                                <div className="gallery-img" key={pic}>
                                    <img
                                        className="responsive-img z-depth-1 "
                                        src={ImgsPath + 'compressed/' + pic + '.jpg'}
                                        alt=''
                                        onClick={() => this.setState(
                                            { isOpen: true, photoIndex: i }
                                        )}
                                    />
                                </div>
                            )
                        }.bind(this)
                    )
                }

                {isOpen && (
                    <Lightbox
                        mainSrc={ImgsPath + images[photoIndex] + '.jpg'}
                        nextSrc={ImgsPath + images[(photoIndex + 1) % images.length] + '.jpg'}
                        prevSrc={ImgsPath + images[(photoIndex + images.length - 1) % images.length] + '.jpg'}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                    />
                )}

            </div>
        )
    }


}
export default Project;


